const cartContent = document.querySelector('.cart__content');
const cartBody = document.querySelector('.cart__body');
const cartEmpty = document.querySelector('.cart__empty');
const cartDelete = document.querySelector('.cart__delete');


// –û—Ç—Å–ª–µ–∂–∏–≤–∞–µ–º –∫–ª–∏–∫ –ø–æ –∫–Ω–æ–ø–∫–µ "–î–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É"
window.addEventListener('click', (event) => {

    if (event.target.hasAttribute('data-cart')){
        const card = event.target.closest('.goods__item');
// –°–æ–±–∏—Ä–∞–µ–º –¥–∞–Ω–Ω—ã–µ –æ –∫–∞—Ä—Ç–æ—á–∫–µ —Ç–æ–≤–∞—Ä–∞
        const productInfo = {
            id: card.dataset.id,
            imgSrc: card.querySelector('.goods__item-image').getAttribute('src'),
            title: card.querySelector('.goods__item-text').innerText,
            price: card.querySelector('.goods__item-discount').innerText,
        }
        
        
// –û—Ç—Ä–∏—Å–æ–≤—ã–≤–µ–º —Ç–æ–≤–∞—Ä, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –∫–ª–∏–∫–Ω—É–ª–∏, –≤ –∫–æ—Ä–∑–∏–Ω–µ. 
        const cartItemHTML = `<article class="cart__item">
        <div class="cart__item-image">
        <img src=${productInfo.imgSrc} alt="Goods-image">
        </div>
        <div class="cart__item-description">
          <p class="item__name">
            ${productInfo.title}
          </p>
          <span class="item__article">–ê—Ä—Ç–∏–∫—É–ª: <span>123654785</span></span>
          <button class="item__delete" data-action = "delete">–£–¥–∞–ª–∏—Ç—å</button>
        </div>
        <div class="item__amount">
          ${productInfo.price}
        </div>
        <div class="item__quantity">
          1 —à—Ç
        </div>
        <div class="item__net-amount">
        ${productInfo.price}
        </div>
      </article>`;
        
      cartBody.insertAdjacentHTML('beforeend', cartItemHTML);
      
      toggleCartStatus();
      calculateTotalPrice();
    }

// –£–¥–∞–ª—è–µ–º —Ç–æ–≤–∞—Ä –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
    if(event.target.closest('.cart__body') && event.target.dataset.action === "delete"){
      event.target.closest(".cart__item").remove();
      toggleCartStatus();
      calculateTotalPrice();
    }
      
})

// –û—Ç–æ–±—Ä–∞–∂–∞–µ–º –∏–ª–∏ —Å–∫—Ä—ã–≤–∞–µ–º –ø–æ–ª–µ "–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞"

function toggleCartStatus() {
  if(cartBody.children.length > 0){
      cartEmpty.classList.add('none')
    } else {
      cartEmpty.classList.remove('none')
    }
}

// –ü–æ–¥—Å—á–µ—Ç –∏—Ç–æ–≥–æ–≤–æ–π —Å—Ç–æ–∏–º–æ—Å—Ç–∏ —Ç–æ–≤–∞—Ä–æ–≤

function calculateTotalPrice(){
  const cartItems = document.querySelectorAll('.cart__item');
  let totalPrice = 0; 

  cartItems.forEach(function (item) {
    const priceEl = item.querySelector('.item__amount');
    totalPrice += parseInt(priceEl.textContent);
  })
  const cartTotalAmount = document.querySelector('.cart__amount');

  cartTotalAmount.innerHTML = totalPrice + ' ‚ÇΩ';

}

// –£–¥–∞–ª—è–µ–º –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã –≤—Å—ë —Å—Ä–∞–∑—É

cartDelete.addEventListener('click', () => {
  cartBody.innerHTML = '';
  toggleCartStatus();
  calculateTotalPrice();
})